import React from "react";
import styled from "styled-components";

const StyledDiv = styled.div`
  display: flex;
  filter: drop-shadow(0 0 20px #180909);

  button {
    display: flex;
    padding: 0;
    border: none;
    background: none;
    font-family: "Staatliches", sans-serif;
    font-size: 24px;
    align-items: center;
    letter-spacing: 1px;
    color: rgba(252, 243, 226, 0.59);

    .flipper-container {
      margin: 0 0 0 10px;
      background: black;
      border-radius: 15px 0 0 15px;
      transition: border-radius 0.33s cubic-bezier(0.33, 1, 0.68, 1);
      box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);

      &.reversed {
        margin: 0 10px 0 0;
        transform: scaleX(-1);
      }

      .flipper {
        height: 32px;
        width: 50px;
        background: radial-gradient(
            80% 80% at 50% 50%,
            rgba(255, 255, 255, 0.08) 0%,
            rgba(0, 0, 0, 0) 100%
          ),
          #1e1e21;
        border-radius: 16px 0 0 16px;
        position: relative;
        transition: transform 0.33s cubic-bezier(0.33, 1, 0.68, 1), filter 0.25s;

        &::before {
          content: "";
          position: absolute;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background: radial-gradient(rgb(255, 130, 130), rgba(255, 0, 0, 1)),
            #1e1e21;
          top: 10px;
          left: 12px;
          border: 1px solid #1f1f1f;
          filter: drop-shadow(0 0 4px red) brightness(1);

          transition: filter 0.25s, background 0.33s;
        }
      }

      &.popped {
        border-radius: 7px 0 0 15px;

        .flipper {
          transform: rotate(14deg) skewX(15deg) scaleY(0.95) translateY(-6px);
          filter: brightness(1.35);

          &::before {
            filter: drop-shadow(0 0 4px transparent) brightness(0.25);
          }
        }
      }
    }
  }
`;

interface ToggleProps {
  leftValue: string;
  rightValue: string;
  value: string;
  onChange: (newValue: string) => void;
}

export default function Toggle({
  leftValue,
  rightValue,
  value,
  onChange,
}: ToggleProps) {
  return (
    <StyledDiv>
      <button type="button" onClick={() => onChange(leftValue)}>
        {leftValue}
        <div
          className={`flipper-container ${value === leftValue ? "" : "popped"}`}
        >
          <div className="flipper" />
        </div>
      </button>

      <button type="button" onClick={() => onChange(rightValue)}>
        <div
          className={`flipper-container reversed ${
            value === rightValue ? "" : "popped"
          }`}
        >
          <div className="flipper" />
        </div>
        {rightValue}
      </button>
    </StyledDiv>
  );
}
