import React from "react";
import styled from "styled-components";
import NakedButton from "./NakedButton";

const StyledDiv = styled.div`
  display: flex;
  flex-wrap: wrap;

  button {
    display: flex;
    align-items: center;

    padding: 4px 16px;
    font-family: "Staatliches", sans-serif;
    font-size: 24px;
    background: radial-gradient(
        80% 80% at 50% 50%,
        rgba(255, 255, 255, 0.08) 0%,
        rgba(0, 0, 0, 0) 100%
      ),
      #1e1e21;

    flex: 1;
    min-width: calc(33.333% - 8px);
    margin: 2px;
    border-radius: 4px;
    color: #fcf3e2;
    border-style: outset;
    border-width: 8px;
    border-color: #44444b;
    box-shadow: 0 0 20px #180909;

    span {
      flex: 1;
    }

    .led {
      content: "";
      width: 10px;
      height: 10px;
      border-radius: 50%;
      border: 1px solid #1f1f1f;

      background: radial-gradient(rgb(255, 130, 130), rgba(255, 0, 0, 1)),
        #1e1e21;
      filter: drop-shadow(0 0 4px transparent) brightness(0.25);

      transition: filter 0.25s, background 0.25s;
    }

    &.active {
      border-width: 4px;
      padding: 10px 20px 6px 20px;

      .led {
        filter: drop-shadow(0 0 4px red) brightness(1);
      }
    }

    transition: border-width 0.1s, padding 0.1s;
  }
`;

interface RadioButtonsProps {
  values: string[];
  value: string;
  onChange: (v: string) => void;
}

export default function RadioButtons({
  values,
  value,
  onChange,
}: RadioButtonsProps) {
  return (
    <StyledDiv>
      {values.map((v, index) => (
        <NakedButton
          key={`${v}-${index}`}
          className={v === value ? "active" : ""}
          onClick={() => onChange(v)}
        >
          <div className="led" />
          <span>{v}</span>
        </NakedButton>
      ))}
    </StyledDiv>
  );
}
