import React, { useEffect, useState } from "react";
import styled from "styled-components";
import {
  progressGoalAtom,
  ProgressMode,
  progressModeAtom,
  putterCountAtom,
} from "../../../recoil/atoms/GameConfig";
import useLocalStorageRecoilState from "../../../hooks/useLocalStorageRecoilState";
import { Link } from "react-router-dom";
import { historyAtom, timeElapsedAtom } from "../../../recoil/atoms/GameState";
import { useResetRecoilState } from "recoil";
import Toggle from "../../common/Toggle";
import Colors from "../../../styles/Colors";
import { ReactComponent as LetsPuttButtonSVG } from "../../../assets/images/LetsPuttButton.svg";
import RadioButtons from "../../common/RadioButtons";

const StyledDiv = styled.div`
  background-color: ${Colors.Brown};
  box-shadow: 0 0 200px #241010 inset;
  letter-spacing: 1px;
  display: flex;
  flex-flow: column nowrap;
  height: 100vh;
  color: rgba(252, 243, 226, 0.8);
  font-family: "Staatliches", sans-serif;

  .header {
    font-family: "Staatliches", sans-serif;
    letter-spacing: 4px;
    font-size: 28px;
    font-weight: 700;
    padding: 4px 8px;
    color: #fcf3e2;
    text-shadow: 0 0 12px black, 1px 1px 0 #8d847e;
  }

  .settings {
    padding: 0 20px;
    margin: 0 8px 8px 8px;
    border-radius: 4px;
    flex: 1;

    .setting-label {
      font-size: 40px;
      margin: 40px 0 8px 0;
      text-shadow: 0 0 8px rgba(252, 243, 226, 0.3);
    }
  }

  .start-button {
    border-radius: 4px 0 0 4px;
    margin: 0 20px 20px 0;
    align-self: flex-end;
    filter: drop-shadow(2px 4px 0 #6c331d)
      drop-shadow(0 4px 8px rgba(0, 0, 0, 0.2));

    svg {
      height: 53px;
    }
  }
`;

export default function GameConfigPage() {
  const [progressMode, setProgressMode] =
    useLocalStorageRecoilState<ProgressMode>(progressModeAtom);

  const [progressCount, setProgressCount] =
    useLocalStorageRecoilState<number>(progressGoalAtom);

  const [isCustomProgressCount, setIsCustomProgressCount] =
    useState<boolean>(false);

  const [putterCount, setPutterCount] =
    useLocalStorageRecoilState<number>(putterCountAtom);

  const resetTimeElapsed = useResetRecoilState(timeElapsedAtom);
  const resetHistory = useResetRecoilState(historyAtom);

  useEffect(() => {
    resetTimeElapsed();
    resetHistory();
  }, [resetHistory, resetTimeElapsed]);

  return (
    <StyledDiv>
      <div className="header">PUTTOMETER</div>
      <div className="settings">
        <div className="setting-label">PUTTER COUNT</div>

        <RadioButtons
          values={["1", "2", "3", "4", "5"]}
          value={putterCount.toString()}
          onChange={(v) => setPutterCount(parseInt(v))}
        />

        <div className="setting-label">POINT TYPE</div>

        <Toggle
          leftValue={ProgressMode.Sinks}
          rightValue={ProgressMode.Throws}
          value={progressMode}
          onChange={(newValue) => setProgressMode(newValue as ProgressMode)}
        />

        <div className="setting-label">POINT GOAL</div>

        <RadioButtons
          values={["50", "100", "200", "500", "CUSTOM"]}
          value={isCustomProgressCount ? "CUSTOM" : progressCount.toString()}
          onChange={(v) => {
            if (v === "CUSTOM") {
              const customVal = parseInt(
                window.prompt("Enter custom amount") ?? ""
              );

              if (!isNaN(customVal)) {
                setIsCustomProgressCount(true);
                setProgressCount(customVal);
              }
            } else {
              setIsCustomProgressCount(false);
              setProgressCount(parseInt(v));
            }
          }}
        />
      </div>

      <Link to="/game" className="start-button">
        <LetsPuttButtonSVG />
      </Link>
    </StyledDiv>
  );
}
